import './App.css';
import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from "react-router-dom";

function Main()
{
  return (
    <h1><Link to ='/'>G-PLAN</Link></h1>
  )
}
function LoginControl()
{
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const navigate = useNavigate();

  useEffect(()=> {
    fetch('/api/checksession')
      .then(response => response.json())
      .then(data => {
        if (data.isLoggedIn) 
        {
          setIsLoggedIn(true);
        }
      });
  }, [isLoggedIn]);

  const handleLogout = () => {
    fetch('/api/logout', {
      method:'POST', 
      headers: {
        'Content-type': 'application/json',
      },
      credentials:'include',
    })
      .then(response => response.json())
      .then(data => {
        if(data.success)
        {
          setIsLoggedIn(false);
          navigate('/');
        }
        else
        {
          console.error(" ");
        }
      })
      .catch(error => {
        console.error('   : ', error);
      });
  };

  return(
    <ul>
      <button id = 'login' className="login_btn" onClick={isLoggedIn ? handleLogout : () => navigate('/login')}>
        {isLoggedIn ? "" : ""}</button>
        {isLoggedIn && (
          <button id='myinformation' className="login_btn">
            <Link to='/myinformation'></Link>
          </button>
      )}    
    </ul>
  );
}
// 
function TestBtn(props, isLoggedIn)
{
  const navigate = useNavigate();
  console.log(props, isLoggedIn);
  return (
    <ul>
      {props.btntype.map((btn, index) => (
        <li key={index}>
          <button className="test_btn"
            onClick={() => 
              {
                if (isLoggedIn) 
                {
                  props.onChangeMode(btn.id);
                  navigate(`/${btn.id}`);
                } 
                else 
                {
                  alert(' .');
                }
              }
            }
          >
            {btn.title}
          </button>
        </li>
      ))}
    </ul>
  );
}
function MyApp() 
{
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const exambtntype = [
    {id : "prevtest", title : " "}, 
    {id : "workbookexam", title : " "}, 
    {id : "wordtest", title : " "}, 
    {id : "record", title : " "}
  ];

  useEffect(()=> {
    fetch('/api/checksession')
      .then(response => response.json())
      .then(data => {
        if (data.isLoggedIn) 
        {
          setIsLoggedIn(true);
        }
      });
  }, [isLoggedIn]);
  return (
    <div className = "background">
      <div className = "wrap">
        <Main/>
        <div className = "login_btn_space">
          <LoginControl/>
        </div>
        <div className='btnsection'>
          <TestBtn btntype = {exambtntype} isLoggedIn={isLoggedIn} onChangeMode = {(id)=>{}} />
        </div>
      </div>
    </div>
  );
} 

export default MyApp;


