import './App.css';
import React from 'react';
import { useState, useRef, useEffect } from 'react';
import Modal from 'react-modal';
import UpdateModal from './UpdateWordModal';

function ManagementWordForm() 
{  
  const [selectedClassification, setSelectedClassification] = useState('');// 
  const [classificationList, setClassificationList] = useState([]);//   
  const [selectedMajor, setSelectedMajor] = useState('');// ()
  const tagRef = useRef(null);
  const [result, setResult] = useState([]);
  const [totalCount, setTotalCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [checkedRows, setCheckedRows] = useState([]);
  const [modalType, setModalType] = useState(null);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  //word_category 
  useEffect(() => {
    const fetchClassificationList = async () => {
      try 
      {
        fetch('/api/get_classification', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            form_type:"word",
            selectedMajor,
          }),
        })
        .then((response) => {
          if (!response.ok) {
            throw new Error('   .');
          }
          return response.json();
        })
        .then((data) => {
          setClassificationList(data.data);
        })
        .catch((error) => {
          console.log('   .', error);
        });;
      } 
      catch (error) 
      {
        console.error('     :', error);
      }
    };

    fetchClassificationList(); // useEffect      
  }, [selectedMajor]);

  const fetchData = (page) => {
    const itemsPerPage = 15; //   
    const offset = (page - 1) * itemsPerPage;

    fetch('/api/search_table', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        selectedClassification,
        selectedMajor,
        offset,
      }),
    })
      .then((response) => {
        if (!response.ok) {
          throw new Error('   .');
        }
        return response.json();
      })
      .then((data) => {
        setResult(data.data);
        setTotalCount(data.totalCount);
        setTotalPages(Math.ceil(data.totalCount / itemsPerPage));
      })
      .catch((error) => {
        console.log('   .', error);
      });
  };

  const handlePageClick = (page) => {
    setCurrentPage(page);
    fetchData(page);
  };

  const handleMajorChange = (e) => {
    setSelectedMajor(e.target.value);
  };

  const handleConfirmButtonClick = () => {
    if (!selectedMajor || selectedMajor === "select") {
      alert('   .');
      return;
    }
    fetchData(1);
  };
  // 
  const handleDeleteButton = () => {
    const confirmation = window.confirm('  ?');
    if(confirmation)
    {
      fetch('/api/delete_word', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          selectedRows: checkedRows,
          selectedMajor,
        }),
      })
      .then((response) => {
        if (!response.ok) {
          throw new Error(`Network response was not ok, status: ${response.status}`);
        }
        return response.json();
      })
      .then((data) => {
        console.log('Response data:', data);
        alert(" ");
        fetchData(1);
      })
      .catch((error) => {
        console.error('Error during fetch operation:', error);
      });
    }
  };

  const openModal = async (type) => {
    Modal.setAppElement('#root');
    setModalType(type);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setModalType(null);
    fetchData(1);
  };

  const handleCheckboxChange = (event, wordCategory , wordId) => {
    const isChecked = event.target.checked;
    if (isChecked) {
      setCheckedRows((prevCheckedRows) => [...prevCheckedRows, { wordCategory, wordId }]);
    } else {
      setCheckedRows((prevCheckedRows) => prevCheckedRows.filter((row) => row.wordId !== wordId));
    }
  };

  const renderModalContent = () => {
    if (modalType === 'update') {
      return (
        <UpdateModal
          modalIsOpen={modalIsOpen}
          closeModal={closeModal}
          handleCheckboxChange={handleCheckboxChange}
          checkedRows={checkedRows}
          selectedLevel = {selectedMajor}
        />
      );
    }
  };
  return (
    <div className='place'>
      <div className="sel_option">
        <div className="category">
          <h3> </h3>
          <select name="category" id="category" value={selectedMajor} onChange={handleMajorChange} required >
            <option value="select"></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div className="category">
          <h3> </h3>
          <select
            id='classification'
            onChange={(e) => setSelectedClassification(e.target.value)}
            value={selectedClassification}
          >
            <option value={'select'}></option>
            {classificationList.map((item) => (
              <option key={item.word_category} value={item.word_category}>
                {item.word_category}
              </option>
            ))}
          </select>
        </div>
          <button className='letter_btn' type='button' onClick={handleConfirmButtonClick}>
          
          </button>
      </div>
      <p>     .</p>
      <table className='third_table'>
          <thead>
          <tr>
              <td></td>
              <td></td>
              <td> </td>
              <td></td>
              <td>1</td>
              <td>2</td>
              <td>3</td>
              <td>4</td>
              <td>5</td>
          </tr>
          </thead>
          <tbody>
          {Array.from({ length: Math.min(result.length, 15) }, (_, i) => (
              <tr key={`${result[i].word_category}_${result[i].word_id}`}>
                <td>
                  <input
                    type="checkbox"
                    onChange={(event) => handleCheckboxChange(event, result[i].word_category, result[i].word_id)}
                  />
                </td>
                <td>{result[i].word_category}</td>
                <td>{result[i].word_id}</td>
                <td>{result[i].word}</td>
                <td>{result[i].word_mean1}</td>
                <td>{result[i].word_mean2}</td>
                <td>{result[i].word_mean3}</td>
                <td>{result[i].word_mean4}</td>
                <td>{result[i].word_mean5}</td>
              </tr>
          ))}
          </tbody>
      </table>
      <div className="paging_number_place">
          {Array.from({ length: totalPages }, (_, i) => (
          <button className="paging_number" key={i + 1} onClick={() => handlePageClick(i + 1)}>
              {i + 1}
          </button>
          ))}
      </div>
      <div className='btn_section'>
          <button className="exam_register" onClick={handleDeleteButton}></button>
          <button className="exam_register" onClick={() => openModal('update')}></button>
          {renderModalContent()}
      </div>
    </div>
  );
      
};

  export default ManagementWordForm;